import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule } from '@ionic/storage';

import { NativeStorage } from '@ionic-native/native-storage';
import { LocalNotifications } from '@ionic-native/local-notifications';

import { FileTransfer } from '@ionic-native/file-transfer';

import { MyApp } from './app.component';
import { AuthServiceProvider } from '../providers/auth-service/auth-service';
import { HttpModule } from '@angular/http';
import { SocialServiceProvider } from '../providers/social-service/social-service';

import { File } from '@ionic-native/file';
import { Transfer } from '@ionic-native/transfer';
import { FilePath } from '@ionic-native/file-path';
import { Camera } from '@ionic-native/camera';
import { SocialSharing } from '@ionic-native/social-sharing';
import { StreamingMedia } from '@ionic-native/streaming-media';

import { UtilsProvider } from '../providers/utils/utils';
import { UserProvider } from '../providers/user/user';
import { QuestServiceProvider } from '../providers/quest-service/quest-service';

@NgModule({
  declarations: [
    MyApp

  ],
  imports: [
    BrowserModule, HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/add-post/add-post.module#AddPostPageModule', name: 'AddPostPage', segment: 'add-post', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/event-feed/event-feed.module#EventFeedPageModule', name: 'EventFeedPage', segment: 'event-feed', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module#HomePageModule', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/landing/landing.module#LandingPageModule', name: 'LandingPage', segment: 'landing', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/leaderboard/leaderboard.module#LeaderboardPageModule', name: 'LeaderboardPage', segment: 'leaderboard', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/line-up/line-up.module#LineUpPageModule', name: 'LineUpPage', segment: 'line-up', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/my-activity/my-activity.module#MyActivityPageModule', name: 'MyActivityPage', segment: 'my-activity', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/notification/notification.module#NotificationPageModule', name: 'NotificationPage', segment: 'notification', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/post-details/post-details.module#PostDetailsPageModule', name: 'PostDetailsPage', segment: 'post-details', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/profile/profile.module#ProfilePageModule', name: 'ProfilePage', segment: 'profile', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/quest/quest.module#QuestPageModule', name: 'QuestPage', segment: 'quest', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/schedule/schedule.module#SchedulePageModule', name: 'SchedulePage', segment: 'schedule', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/signup/signup.module#SignupPageModule', name: 'SignupPage', segment: 'signup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/social-wall/social-wall.module#SocialWallPageModule', name: 'SocialWallPage', segment: 'social-wall', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sponsors/sponsors.module#SponsorsPageModule', name: 'SponsorsPage', segment: 'sponsors', priority: 'low', defaultHistory: [] }
  ]
}),
      IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    StatusBar,
    SplashScreen,
    AuthServiceProvider,
    SocialServiceProvider,
    File,
    Transfer,
    FileTransfer,
    Camera,
    FilePath,
    SocialSharing,
    NativeStorage,
    LocalNotifications,
    StreamingMedia,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    UtilsProvider,
    UserProvider,
    QuestServiceProvider
  ]
})
export class AppModule {}



